	module.exports = {"data":{"markdownRemark":{"html":"<h3>What is it?</h3>\n<p>The 90 day leaderboard shows the top 100 hackers based on their activity in the last 90 days.</p>\n<h3>How do I access it?</h3>\n<p>Go to <a href=\"https://hackerone.com/leaderboard/\">https://hackerone.com/leaderboard/</a> and make sure that the filter is set to 90 days.</p>\n<h3>How do I become eligible for the leaderboard?</h3>\n<p>You’ll be eligible if within the last 90 days you have:</p>\n<ul>\n<li>Positive reputation gain</li>\n<li>Non-negative signal</li>\n<li>Zero code of conduct violations</li>\n</ul>\n<h3>How do I move up in ranking?</h3>\n<p>Hackers are ranked by their score from the calculation:\n<strong>Reputation x Signal Percentile x Impact Percentile</strong></p>\n<ul>\n<li><strong>Reputation</strong> is based on your report validity.</li>\n<li><strong>Signal</strong> and <strong>Impact Percentile</strong> are calculated against all eligible hackers.\nIncreasing your reputation, signal, or impact will move you up on the leaderboard. As the leaderboard updates every day at 8:30am UTC, you can see how you rank with other hackers on a daily basis.</li>\n</ul>\n<h3>Why should I care about my ranking?</h3>\n<p>The higher your rank, the higher your chance of receiving invitations to private programs. </p>\n<p>For instance, if this is the state of the 90 day leaderboard:</p>\n<ol>\n<li>Alice</li>\n<li>Bob</li>\n<li>Charlie</li>\n<li>David</li>\n</ol>\n<p>For each invitation that goes out, the chances for each hacker receiving the invite would be:</p>\n<ol>\n<li>Alice: 40%</li>\n<li>Bob: 30%</li>\n<li>Charlie: 20%</li>\n<li>David: 10%</li>\n</ol>\n<p><em>Note: Hackers in the priority queue will still be first to receive invitations.</em></p>","frontmatter":{"path":"/hackers/the-90-day-leaderboard","title":"The 90 Day Leaderboard"}}},"pathContext":{}}

